import React from "react";
import Table from "../../components/Table/Table";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import { FC_IsJson } from "../../utils/functions";

type DisplayCompetenceProps = {
  competence: string;
  displayType?: "table" | "text" | "text-one";
  hideColumns?: boolean;
  thColor?: "blue" | "gray";
};

const DisplayCompetence: React.FC<DisplayCompetenceProps> = ({
  competence = "",
  displayType = "table",
  thColor = "blue",
  hideColumns = false,
}) => {
  const position_description: string[] =
    isEmptyOrSpaces(competence) || !FC_IsJson(competence)
      ? []
      : JSON.parse(competence);

  if (!isEmptyOrSpaces(competence) && !FC_IsJson(competence))
    return <span>{competence}</span>;
  if (position_description.length === 0)
    return <span>No competence available!</span>;

  if (displayType === "text-one") return <span>{position_description[0]}</span>;

  if (displayType === "text")
    return (
      <>
        {position_description.map((item, i) => (
          <span key={i + item}>
            {item + (i < position_description.length - 1 ? "," : "")}
          </span>
        ))}
      </>
    );

  const competenceTableData = () => {
    if (position_description.length === 0) {
      return [
        <tr key={Math.random()} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap" colSpan={2}>
            No competence Found
          </td>
        </tr>,
      ];
    }

    return position_description.map((item, index) => (
      <tr
        key={index + item}
        className="border-b border-gray-200 hover:bg-gray-100 group"
      >
        <td className="px-3 py-3 text-left">{index + 1}</td>
        <td className="px-3 py-3 text-left">{item}</td>
      </tr>
    ));
  };

  return (
    <>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          {
            columnTitle: "Competence",
            alignText: "left",
            padding: "p-3",
          },
        ]}
        rows={competenceTableData}
        pagination={false}
        search={false}
        hideColumns={hideColumns}
        thColor={thColor}
      />
    </>
  );
};

export default React.memo(DisplayCompetence);
