import React from "react";
import { connect } from "react-redux";
import Table from "../../components/Table/Table";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import { PositionRequirementTableInterface } from "./ViewPosition";

interface RequirementProps {
  requirementsArray: PositionRequirementTableInterface[];
  document: DocumentReducerInterface;
}

const ReviewRequirements: React.FC<RequirementProps> = (props) => {
  const tableData = (): JSX.Element[] => {
    let data: PositionRequirementTableInterface[] = props.requirementsArray;
    // let data: positionRequirementInterface[] = props.requirement;
    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={3}>
            No Requirement Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr
        key={index + item.field_id}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>{item.subcategory_title}</span>
        </td>
        <td className="p-3 text-left">
          <span>
            {isEmptyOrSpaces(item.field_id) ? "Any document" : item.field_title}
          </span>
        </td>
        <td className="p-3 text-left text-lg italic">
          {isEmptyOrSpaces(item.field_id) ? (
            item.subcategory_required ? (
              <span className="text-yellow-800">Required</span>
            ) : (
              "Optional"
            )
          ) : item.required ? (
            <span className="text-yellow-800">Required</span>
          ) : (
            "Optional"
          )}
        </td>
      </tr>
    ));
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          { columnTitle: "Qualification", alignText: "left", padding: "p-3" },
          { columnTitle: "Document", alignText: "left", padding: "p-3" },
          { columnTitle: "Status", alignText: "left", padding: "p-3" },
          // { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={false}
        thColor="white"
      />
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return {
    document,
  };
};
export default React.memo(connect(mapStateToProps)(ReviewRequirements));
