import axios from "axios";
import React, { useEffect, useState } from "react";
import { MdTurnedIn } from "react-icons/md";
import { VscLoading } from "react-icons/vsc";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import { getDocumentSubCategory } from "../../actions/document";
import { publishPosition } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import Loading from "../../components/Loading/Loading";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces/position";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/functions";
import DisplayCompetence from "./DisplayCompetence";
import { getQualificationById } from "./StepsReview";
import ViewRequirements from "./ViewRequirements";

export interface PositionRequirementTableInterface {
  category_id: string;
  category_title: string;
  subcategory_id: string;
  subcategory_title: string;
  field_title: string;
  field_id: string;
  // _id: string;
  required: boolean;
  subcategory_required: boolean;
}

export const FC_PositionDistinctCategory = (
  PositionData: PositionAPI[]
): {
  PositionsTemp: PositionRequirementTableInterface[];
  DistinctCategoriesTemp: PositionRequirementTableInterface[];
  DistinctSubCategoriesTemp: PositionRequirementTableInterface[];
} => {
  let PositionsTemp: PositionRequirementTableInterface[] = [];
  let DistinctCategoriesTemp: PositionRequirementTableInterface[] = [],
    DistinctCategoriesIds: string[] = [];
  let DistinctSubCategoriesTemp: PositionRequirementTableInterface[] = [],
    DistinctSubCategoriesIds: string[] = [];
  // LOOP IN ALL POSITIONS ==========================
  for (let pos in PositionData) {
    // GET CURRENT POSITION ====================
    let thisPosition = PositionData[pos];
    // GET REQUIREMENTS OF THE CURRENT POSITION ====================
    let requirements = thisPosition.requirements;
    // LOOP THROUGH CURRENT POSITION REQUIREMENTS ==========================
    for (let req in requirements) {
      let thisRequirements = requirements[req];
      // GET DOC CATEGORY OF THE CURRENT POSITION REQUIREMENTS ====================
      let thisCategory = thisRequirements.category;
      // GET DOC SUB CATEGORY OF THE CURRENT POSITION REQUIREMENTS ====================
      let thisSubCategory = thisRequirements.subcategory;
      // LOOP THROUGH FIELDS OF THE CURRENT POSITION REQUIREMENTS ==========================
      if (thisCategory) {
        if (thisRequirements.position_requirement.length > 0) {
          for (let field in thisRequirements.position_requirement) {
            // GET FIELDS OF THE CURRENT POSITION REQUIREMENTS ====================
            let thisField = thisRequirements.position_requirement[field];
            // ADD CURRENT CATEGORY, SUB CATEGORY & FIELDS OF THE CURRENT POSITION REQUIREMENTS IN POSITIONS TEMP ====================
            PositionsTemp.push({
              category_id: thisCategory.user_detail_category_id,
              category_title: thisCategory.title,
              subcategory_id: thisSubCategory.user_detail_subcategory_id,
              subcategory_title: thisSubCategory.title,
              subcategory_required: thisRequirements.required,
              field_title: thisField.title,
              field_id: thisField._id,
              required: thisField.required,
            });
          }
        } else {
          PositionsTemp.push({
            category_id: thisCategory.user_detail_category_id,
            category_title: thisCategory.title,
            subcategory_id: thisSubCategory.user_detail_subcategory_id,
            subcategory_title: thisSubCategory.title,
            subcategory_required: thisRequirements.required,
            field_title: "",
            field_id: "",
            required: false,
          });
        }

        // ADD CATEOGRY OF THE CURRENT POSITION REQUIREMENTS TO ARRAY ====================
        if (
          !DistinctCategoriesIds.includes(thisCategory.user_detail_category_id)
        ) {
          DistinctCategoriesIds.push(thisCategory.user_detail_category_id);
          DistinctCategoriesTemp.push({
            category_id: thisCategory.user_detail_category_id,
            category_title: thisCategory.title,
            subcategory_id: "",
            subcategory_title: "",
            field_title: "",
            field_id: "",
            required: false,
            subcategory_required: thisRequirements.required,
          });
        }

        // ADD SUB CATEOGRY OF THE CURRENT POSITION REQUIREMENTS TO ARRAY ====================
        if (
          !DistinctSubCategoriesIds.includes(
            thisSubCategory.user_detail_subcategory_id
          )
        ) {
          DistinctSubCategoriesIds.push(
            thisSubCategory.user_detail_subcategory_id
          );
          DistinctSubCategoriesTemp.push({
            category_id: thisSubCategory.category_id,
            category_title: "",
            subcategory_id: thisSubCategory.user_detail_subcategory_id,
            subcategory_title: thisSubCategory.title,
            subcategory_required: thisRequirements.required,
            field_title: "",
            field_id: "",
            required: false,
          });
        }
      }
    }
    // DistinctCategoriesIds.;
  }

  return { PositionsTemp, DistinctCategoriesTemp, DistinctSubCategoriesTemp };
};

export interface ViewPositionProps {
  thisPosition: PositionAPI;
  setThisPosition?: (value: PositionAPI | null) => void;
  isGuest?: boolean;

  document: DocumentReducerInterface;
  getDocumentSubCategory: Function;
  publishPosition: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewPosition = (props: ViewPositionProps) => {
  const [loadingRequest, setLoadingRequest] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    if (!props.document.documentSubCategory) {
      props.getDocumentSubCategory();
    } else {
      setLoading(false);
    }
  }, [props]);

  if (loading) {
    return <Loading />;
  }

  const publishPosition = async (
    position_id: string,
    status: boolean,
    name: string
  ) => {
    if (
      window.confirm(
        `Are you sure do you want to ${
          status ? "publish" : "unpublish"
        } this position?`
      )
    ) {
      setLoadingRequest(position_id);
      try {
        const res = await axios.patch(
          `${API.APPLICATION}/publishposition/${position_id}`,
          {
            isPublished: status,
          }
        );
        props.publishPosition({ position_id, status });
        props.PushPopupAlert({
          title: `${name} ${
            status ? " published" : " stopped on application"
          } successfully`,
          theme: PopupAlertTheme.info,
        });
        let thisPositionCopy = { ...props.thisPosition };
        thisPositionCopy.isPublished = status;
        props.setThisPosition && props.setThisPosition(thisPositionCopy);
        console.log("Position publish: ", res);
      } catch (error) {
        console.error("err: ", error);
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadingRequest("");
    }
  };

  return (
    <div className="w-full">
      <div className="w-full bg-gray-100 px-4 py-1 animate__animated animate__fadeIn">
        <div className="flex flex-col mb-2">
          <span className="text-blue-600 font-bold text-2xl">
            {props.thisPosition.position_title}
          </span>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Code:</span>
            <span className="font-semibold">
              {props.thisPosition.position_code}
            </span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>School level:</span>
            <span className="font-semibold">
              {props.thisPosition.school_level_id.school_level_name}
            </span>
          </div>
          {!props.isGuest && (
            <div className="flex items-center gap-2 text-base text-gray-600">
              <span className={`italic`}>Primary qualification:</span>
              <span className="font-semibold">
                {props.document.documentSubCategory &&
                getQualificationById(
                  props.document.documentSubCategory,
                  props.thisPosition.subcategory_id
                )
                  ? getQualificationById(
                      props.document.documentSubCategory,
                      props.thisPosition.subcategory_id
                    )!.title
                  : "Undefined"}
              </span>
            </div>
          )}
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Institute:</span>
            <span className="font-semibold">
              {
                props.thisPosition.education_institute_id
                  .education_category_title
              }
            </span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600 overflow-x-auto">
            <span className={`italic`}>Courses:</span>
            <span className="flex items-center gap-2">
              {props.thisPosition.courses.map((item, index) => (
                <div
                  key={index + item.course_id}
                  className="flex items-center gap-1"
                >
                  <span className="font-semibold">{item.course_name}</span>
                  <span className="text-gray-600">({item.course_code})</span>
                  {index !== props.thisPosition.courses.length - 1 && ","}
                </div>
              ))}
            </span>
          </div>
          {!props.isGuest && (
            <div className="flex items-center gap-2 text-base text-gray-600">
              <span className={`italic`}>Status:</span>

              <div className="flex flex-row items-center gap-2 mt-1">
                <div
                  className={`relative ${
                    loadingRequest.length > 0
                      ? "cursor-not-allowed"
                      : "cursor-pointer"
                  } flex items-center gap-1`}
                  onClick={() =>
                    !loadingRequest.length &&
                    !props.isGuest &&
                    publishPosition(
                      props.thisPosition.position_id,
                      !props.thisPosition.isPublished,
                      props.thisPosition.position_title
                    )
                  }
                >
                  <span className="block w-14 h-8 bg-gray-100 hover:border-gray-100 rounded-full shadow-inner"></span>
                  <span
                    className={`absolute block w-6 h-6 mt-1 ml-1 rounded-full shadow inset-y-0 left-0 focus-within:shadow-outline transition-transform duration-500 ease-in-out ${
                      !props.thisPosition.isPublished
                        ? "bg-yellow-500"
                        : "bg-green-500 transform translate-x-full"
                    }`}
                  ></span>
                  {loadingRequest === props.thisPosition.position_id ? (
                    <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
                  ) : (
                    <span className="font-medium">
                      {props.thisPosition.isPublished
                        ? "Published"
                        : "Unpublished"}
                    </span>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="bg-white w-full rounded p-2 mb-2">
          <div className="text-blue-500 text-2xl font-bold my-2">
            Position Competences
          </div>

          <DisplayCompetence
            competence={props.thisPosition.description}
            thColor="gray"
          />
        </div>

        <div className="bg-white w-full rounded p-2 mb-2">
          <div className="text-blue-500 text-2xl font-bold my-2">
            Position Requirements
          </div>
          {props.thisPosition.requirements.length > 0 ? (
            FC_PositionDistinctCategory([
              props.thisPosition,
            ]).DistinctCategoriesTemp.map((item, index) => (
              <div
                className="bg-gray-100 rounded p-2"
                key={index + item.category_id}
              >
                <div className="flex items-center gap-2 text-2xl text-gray-500 font-bold">
                  <MdTurnedIn />
                  <span>{item.category_title}</span>
                </div>
                <div className="border bg-white mt-1">
                  <ViewRequirements
                    requirements={FC_PositionDistinctCategory([
                      props.thisPosition,
                    ]).PositionsTemp.filter(
                      (req) => req.category_id === item.category_id
                    )}
                  />
                </div>
              </div>
            ))
          ) : (
            <NoResult
              theme={NoResultThemes.Dark}
              value={"No requirements"}
            ></NoResult>
          )}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return {
    document,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    getDocumentSubCategory,
    publishPosition,
    PushPopupAlert,
  })(ViewPosition)
);
