import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { MdTurnedIn } from "react-icons/md";
import { connect } from "react-redux";
import { positionRequirementInterface } from ".";
import { PushPopupAlert } from "../../actions";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../actions/document";
import { getPositions, PositionCreateInterface } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import Loading from "../../components/Loading/Loading";
import { course } from "../../interfaces/course";
import { education_category } from "../../interfaces/education_category";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces/position";
import { school_level } from "../../interfaces/school_level";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import DisplayCompetence from "./DisplayCompetence";
import ReviewRequirements from "./ReviewRequirements";
import { PositionRequirementTableInterface } from "./ViewPosition";

export const getQualificationById = (
  data: DocumentSubCategoryInterface[],
  qualificationId: string
): DocumentSubCategoryInterface | undefined => {
  return data.find(
    (item) => item.user_detail_subcategory_id === qualificationId
  );
};

interface StepsReviewProps {
  modalAction: false | "CREATE_POSITION" | "UPDATE_POSITION";
  positionId: string;
  positionTitle: string;
  positionCode: string;
  positionDesc: string;
  positionSubCategoryId: string;
  thisLevel: school_level;
  thisEdication: education_category;
  thisCourses: course[];
  thisCoursesId: string[];
  thisRequirementList:
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
    FOUR: boolean;
  };
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  systemPositions: PositionAPI[] | null;
  setModalAction: React.Dispatch<
    React.SetStateAction<false | "CREATE_POSITION" | "UPDATE_POSITION">
  >;
  setThisPosition: React.Dispatch<React.SetStateAction<PositionAPI | null>>;
  document: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getPositions: Function;
  cleanStates: () => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const StepsReview: React.FC<StepsReviewProps> = (props) => {
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    if (!props.document.documentCategory) {
      props.getDocumentCategory();
    } else if (!props.document.documentSubCategory) {
      props.getDocumentSubCategory();
    } else if (!props.document.documentFields) {
      props.getDocumentFields();
    } else {
      setLoading(false);
    }
  }, [props]);

  const handleSubmit = async () => {
    setLoadingRequest(true);
    if (!props.thisRequirementList) {
      return props.PushPopupAlert({
        title: `Please add requirement`,
        theme: PopupAlertTheme.warning,
      });
    }

    try {
      // Update current
      if (props.modalAction === "UPDATE_POSITION") {
        const api_body: PositionCreateInterface = {
          position_id: props.positionId,
          position_title: props.positionTitle,
          position_code: props.positionCode,
          description: props.positionDesc,
          subcategory_id: props.positionSubCategoryId,
          school_level_id: props.thisLevel.school_level_id,
          education_institute_id: props.thisEdication.education_institute_id,
          courses: props.thisCoursesId,
          requirements: props.thisRequirementList,
        };
        const res = await axios.patch(`${API.APPLICATION}/position`, api_body);
        console.log("==================UPDATE POSITION==================");
        console.log({
          update_position: api_body,
        });
        console.log({ "Result: ": res });
        console.log("===================UPDATE POSITION=================");

        //   props.updateApplicationSession(res.data.data);
        props.getPositions((status: boolean, msg: string) => {
          setLoadingRequest(status);
          if (msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        });

        props.PushPopupAlert({
          title: `Position updated successfully`,
          theme: PopupAlertTheme.info,
        });
        props.systemPositions && props.setThisPosition(null);
        props.cleanStates();
        props.setModalAction(false);
      } else {
        const api_body: PositionCreateInterface = {
          position_title: props.positionTitle,
          position_code: props.positionCode,
          description: props.positionDesc,
          school_level_id: props.thisLevel.school_level_id,
          subcategory_id: props.positionSubCategoryId,
          education_institute_id: props.thisEdication.education_institute_id,
          courses: props.thisCoursesId,
          requirements: props.thisRequirementList,
        };
        console.log("==================CREATE POSITION==================");
        console.log({
          create_position: api_body,
        });
        console.log("==================CREATE POSITION==================");
        // Add new
        const res = await axios.post(`${API.APPLICATION}/position`, api_body);
        //   props.createAplicationSession(res.data.session);
        props.getPositions((status: boolean, msg: string) => {
          setLoadingRequest(status);
          if (msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        });
        props.PushPopupAlert({
          title: `Position created successfully`,
          theme: PopupAlertTheme.success,
        });
        //   props.closeModal(false);
        props.cleanStates();
        props.setModalAction(false);
        console.log("CREATING_NEW_POSITION: ", res);
      }
    } catch (error) {
      props.PushPopupAlert({
        title: `${errorToText(error)}! Try again`,
        theme: PopupAlertTheme.danger,
      });
      console.error("Err: ", { error });
      setLoadingRequest(false);
    }
  };

  if (loading) return <Loading />;

  const FC_GroupRequirementsByCategory = (
    thisRequirementList: {
      user_detail_category_id: string;
      user_detail_subcategory_id: string;
      position_requirement: positionRequirementInterface[];
      required: boolean;
    }[]
  ): {
    PositionsTemp: PositionRequirementTableInterface[];
    DistinctCategoriesTemp: PositionRequirementTableInterface[];
    DistinctSubCategoriesTemp: PositionRequirementTableInterface[];
  } => {
    let PositionsTemp: PositionRequirementTableInterface[] = [];
    let DistinctCategoriesTemp: PositionRequirementTableInterface[] = [],
      DistinctCategoriesIds: string[] = [];
    let DistinctSubCategoriesTemp: PositionRequirementTableInterface[] = [],
      DistinctSubCategoriesIds: string[] = [];
    // GET DISTINCT CATEGORIES && SUB CATEGORIES ==========================
    for (const key in thisRequirementList) {
      if (
        props.document.documentCategory &&
        props.document.documentSubCategory &&
        props.document.documentFields
      ) {
        // ADD CATEOGRY TO ARRAY ====================
        let thisCategory: DocumentCategoryInterface | undefined =
            props.document.documentCategory.find(
              (item) =>
                item.user_detail_category_id ===
                thisRequirementList[key].user_detail_category_id
            ),
          thisSubCategory: DocumentSubCategoryInterface | undefined =
            props.document.documentSubCategory.find(
              (item) =>
                item.user_detail_subcategory_id ===
                thisRequirementList[key].user_detail_subcategory_id
            );

        if (thisCategory && thisSubCategory) {
          if (
            !DistinctCategoriesIds.includes(
              thisRequirementList[key].user_detail_category_id
            )
          ) {
            DistinctCategoriesIds.push(
              thisRequirementList[key].user_detail_category_id
            );
            DistinctCategoriesTemp.push({
              category_id: thisRequirementList[key].user_detail_category_id,
              category_title: thisCategory.title,
              subcategory_id: "",
              subcategory_title: "",
              subcategory_required: thisRequirementList[key].required,
              field_title: "",
              field_id: "",
              required: false,
            });
          }

          // ADD SUB CATEOGRY TO ARRAY ====================
          if (
            !DistinctSubCategoriesIds.includes(
              thisRequirementList[key].user_detail_subcategory_id
            )
          ) {
            DistinctSubCategoriesIds.push(
              thisRequirementList[key].user_detail_subcategory_id
            );
            DistinctSubCategoriesTemp.push({
              category_id: thisRequirementList[key].user_detail_category_id,
              category_title: "",
              subcategory_id:
                thisRequirementList[key].user_detail_subcategory_id,
              subcategory_title: thisSubCategory.title,
              subcategory_required: false,
              field_title: "",
              field_id: "",
              required: false,
            });
          }

          // GET ALL REQUIREMENTS ====================================
          if (thisRequirementList[key].position_requirement.length > 0) {
            for (const field in thisRequirementList[key].position_requirement) {
              const thisField =
                thisRequirementList[key].position_requirement[field];
              PositionsTemp.push({
                category_id: thisCategory.user_detail_category_id,
                category_title: thisCategory.title,
                subcategory_id: thisSubCategory.user_detail_subcategory_id,
                subcategory_title: thisSubCategory.title,
                subcategory_required: thisRequirementList[key].required,
                field_title: props.document.documentFields.find(
                  (item) => item._id === thisField.category_subcategory_data_id
                )
                  ? props.document.documentFields.find(
                      (item) =>
                        item._id === thisField.category_subcategory_data_id
                    )!.title
                  : "",
                field_id: thisField.category_subcategory_data_id,
                required: thisField.required,
              });
            }
          } else {
            PositionsTemp.push({
              category_id: thisCategory.user_detail_category_id,
              category_title: thisCategory.title,
              subcategory_id: thisSubCategory.user_detail_subcategory_id,
              subcategory_title: thisSubCategory.title,
              subcategory_required: thisRequirementList[key].required,
              field_title: "",
              field_id: "",
              required: false,
            });
          }
        }
      }
    }

    return { PositionsTemp, DistinctCategoriesTemp, DistinctSubCategoriesTemp };
  };

  return (
    <Fragment>
      <div className="w-full px-4 py-1 mt-4 animate__animated animate__fadeIn">
        <div className="flex flex-col mb-2">
          <span className="text-blue-600 font-bold text-2xl">
            {props.positionTitle}
          </span>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Code:</span>
            <span className="font-semibold">{props.positionCode}</span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>School level:</span>
            <span className="font-semibold">
              {props.thisLevel.school_level_name}
            </span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Primary qualification:</span>
            <span className="font-semibold">
              {props.document.documentSubCategory &&
              getQualificationById(
                props.document.documentSubCategory,
                props.positionSubCategoryId
              )
                ? getQualificationById(
                    props.document.documentSubCategory,
                    props.positionSubCategoryId
                  )!.title
                : "Undefined"}
            </span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Institute:</span>
            <span className="font-semibold">
              {props.thisEdication.education_category_title}
            </span>
          </div>
          <div className="flex items-center gap-2 text-base text-gray-600">
            <span className={`italic`}>Courses:</span>
            <span className="flex items-center gap-2">
              {props.thisCourses.map((item, index) => (
                <div
                  key={index + item.course_id}
                  className="flex items-center gap-1"
                >
                  <span className="font-semibold">{item.course_name}</span>
                  <span className="text-gray-600">({item.course_code})</span>
                  {index !== props.thisCourses.length - 1 && ","}
                </div>
              ))}
            </span>
          </div>
        </div>
        <div className="bg-white w-full rounded p-2 mb-2">
          <div className="text-blue-500 text-2xl font-bold my-2">
            Position Competence
          </div>

          <DisplayCompetence competence={props.positionDesc} thColor="gray" />
        </div>

        <div className="bg-white w-full rounded p-2 mb-2">
          <div className="text-blue-500 text-2xl font-bold my-2">
            Position Requirements
          </div>

          {props.thisRequirementList && props.thisRequirementList.length > 0 ? (
            FC_GroupRequirementsByCategory(
              props.thisRequirementList
            ).DistinctCategoriesTemp.map((item, index) => (
              <div
                className="bg-gray-100 rounded p-2"
                key={index + item.category_id}
              >
                <div className="flex items-center gap-2 text-2xl text-gray-500 font-bold">
                  <MdTurnedIn />
                  <span>{item.category_title}</span>
                </div>
                <div className="border bg-white mt-1">
                  {props.thisRequirementList && (
                    <ReviewRequirements
                      requirementsArray={FC_GroupRequirementsByCategory(
                        props.thisRequirementList
                      ).PositionsTemp.filter(
                        (req) => req.category_id === item.category_id
                      )}
                    />
                  )}
                </div>
              </div>
            ))
          ) : (
            <div>No Data</div>
          )}
        </div>
      </div>

      <div className="flex items-center justify-between mt-4">
        <button
          className="btn bg-gray-500 text-gray-50 mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() =>
            props.setFormSteps({
              ONE: false,
              TWO: false,
              THREE: true,
              FOUR: false,
            })
          }
        >
          Back
        </button>
        <button
          className="btn btn-blue mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() => handleSubmit()}
          disabled={loadingRequest}
        >
          {loadingRequest ? "Loading..." : "Save"}
        </button>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  document,
  system_settings,
}: StoreState): {
  document: DocumentReducerInterface;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    document,
    systemPositions: system_settings.systemPositions,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    getDocumentCategory,
    getDocumentSubCategory,
    getDocumentFields,
    getPositions,
    PushPopupAlert,
  })(StepsReview)
);
